import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Typography, Grid, Tabs, Tab } from '@material-ui/core';
import logo from '../resource/logo-visimages.png';
import patternData from '../resource/pattern_data.json'

const colors = {
    'juxtaposed': "#3977AF",
    'overlaid': "#EF8536",
    'nested': "#519C3E"
};

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    height:'92%',
    display: 'flex',
    flexDirection: 'column',
    alignItems:'center',
    backgroundColor:'lightgray',
    overflowY:'scroll'
  },
  content:{
    position:'relative',
    width: '70%',
    display: 'flex',
    flexDirection: 'column',
    alignItems:'center',
  },
    indicator: {
    backgroundColor: "#3977AF",
    },
  tabs:{
      width: '100%',
  },
  parentDef:{
    width:'100%',
    backgroundColor:'white',
    borderRadius:'5px',
  },
  childDef:{
    width:'100%',
  },
  blockWrap:{
    width: '100%',
    padding: '10px 0 0 0',
    display: 'flex',

  },
  icon:{
    margin:'0 10px 0 0',
    width: '5vw',
    height:'5vw',
    backgroundColor:'white',
    borderRadius:'5px',
  },
  textWrap:{
    width:'100%',
    padding:'20px',
    backgroundColor:'white',
    borderRadius:'5px',
  },
  name:{
    fontSize: '24px',
    margin: '0 0 24px'
  },
  definition:{
    fontSize: '16px',
    margin: '16px 0'
  },
  example:{
      width: '100%',
    // width: '50px',
    // height:'50px'
  },
}));

function PatternBlock({icon, name, definition, figure}) {
    const classes = useStyles();
    return (
        <div className={classes.blockWrap}>
            <img src={icon} className={classes.icon}></img>
            <div className={classes.textWrap}>
                <Typography className={classes.name}>
                    {name}
                </Typography>
                <Typography className={classes.definition}>
                    {definition}
                </Typography>
                <img src={figure} className={classes.example}></img>
            </div>
        </div>
    )
}

function DesignSuggestions() {
  const classes = useStyles();
  console.log(patternData);
  const [key, setValue] = React.useState('juxtaposed');

  const handleChange = (event, newValue) => {
    setValue(newValue);
  }

  return (
    <div className={classes.root}>
        <div className={classes.content}>
            <Tabs className={classes.tabs}
                value={key}
                indicatorColor="primary"
                onChange={handleChange}
                centered
            >
                {Object.keys(patternData).map(value=>(<Tab value={value} label={value} {...patternData[value].name}/>))}
            </Tabs>
            <div className={classes.parentDef}>
                <Typography style={{
    fontSize: '24px',
    margin: '24px'}}>
                    {patternData[key].name}
                </Typography>
                <Typography style={{
    fontSize: '16px',
    margin: '16px 24px 20px 24px'}}>
                    {patternData[key].definition}
                </Typography>
            </div>
            <div className={classes.childDef}>
            {
                patternData[key].subtypes.map(subtype => {
                    const {id, name, definition } = subtype;
                    console.log(id, definition);
                    return (<PatternBlock 
                        icon={logo}
                        name={name} 
                        definition={definition} 
                        figure={process.env.PUBLIC_URL + `/patterns/${id}.svg`}/>)
                })
            }
            </div>
            {/* <PatternBlock 
                icon={logo}
                name={"Accompanied Visualization"} 
                definition='In an accompanied visualization, component visualizations share the same coordinate system.' 
                figure={acc}/> */}
        </div>
    </div>
  );
}


export default DesignSuggestions;